@import "https://fonts.googleapis.com/css2?family=Fira+Code:wght@300;400;500;600;700&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --body-backgroud-color: black;
  --terminal-green: #8ae234;
  --terminal-blue: #729fce;
  --terminal-white: #fff;
  --terminal-grey: #666;
  --saffron: #f93;
  --white: #fff;
  --green: #8ae234;
  --red: #d52a1f;
  --light-grey: #b4b4b4;
  --link-color: #4ebcf7;
}

body {
  background-color: var(--body-backgroud-color);
  font-family: Fira Code, monospace;
  overflow-x: hidden;
}

a {
  text-decoration: none;
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: #181818;
}

::-webkit-scrollbar-thumb {
  background: var(--terminal-grey);
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: #555;
}

